:root {
  --font-plex: "ibm-plex-mono", sans-serif;
}

:root {
  --black: rgb(30, 30, 30);
  --gray: rgb(232, 232, 232);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-plex);
  font-size: 0.75rem;
  font-style: normal;
  line-height: 1.2;
  color: var(--black);
  background: var(--gray);
}

.container {
  padding: 1rem;
}

.archive {
  display: flex;
  flex-wrap: wrap;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--black);
}

.sidebar {
  flex-basis: 20rem;
  flex-grow: 1;
  flex-flow: column nowrap;
  max-width: 15.625rem;
  height: 100vh;
  border-right: solid 1px black;
}
.sidebar #nsv-logo {
  fill: var(--black);
  width: 5rem;
}

.body {
  flex-basis: 0;
  flex-grow: 999;
  min-width: 50%;
  height: 100vh;
  overflow: scroll;
}